<template>
  <section class="page">
    <comment-list></comment-list>
    <p id="bottom"></p>
  </section>
</template>

<script>
  import { CommentList } from '@/modules/Comment'

  export default {
    name: 'PhotoCommentIndex',

    components: {
      CommentList
    },

    beforeRouteEnter (to, from, next) {
      next(vm => {
        if (from.name !== 'photo.comment.childcomment.index') {
          setTimeout(() => {
            document.querySelector(to.hash).scrollIntoView()
            if (vm.$route.query.comment === 'create') document.commentForm.content.focus()
          }, 200)
        }
      })
    }
  }
</script>



